main,
.dialog {
    .cta-block {
        list-style: none;
        margin: 0;
    }

    .primary-cta-arrow--bright {
        @include cta-primary-icon-light-bg();
    }

    .primary-cta--bright {
        @include cta-primary-light-bg();
    }

    .primary-cta-arrow--dark {
        @include cta-primary-icon-dark-bg();
    }

    .primary-cta--dark {
        @include cta-primary-dark-bg();
    }

    .outline-cta-arrow--bright {
        @include cta-outlined-icon-light-bg();
    }

    .outline-cta--bright {
        @include cta-outlined-light-bg();
    }

    .outline-cta-arrow--dark {
        @include cta-outlined-icon-dark-bg();
    }

    .outline-cta--dark {
        @include cta-outlined-dark-bg();
    }

    .basic-cta--bright {
        @include cta-basic-light-bg();
    }

    .basic-cta--dark {
        @include cta-basic-dark-bg();
    }

    .primary-cta--grey--bright {
        @include cta-primary-light-bg($color-gray3, $color-nearblack);

        &:hover,
        &:active {
            color: $color-purewhite;
        }
    }

    .basic-cta--lifelong-learning-primary {
        @include cta-primary-icon-light-bg($color-pink90);
    }

    .basic-cta--lifelong-learning-secondary {
        @include cta-basic-light-bg($color-pink90);
    }

    .primary-cta--grey--bright {
        @include cta-primary-dark-bg($color-gray3, $color-nearblack);
        &:hover,
        &:active {
            color: $color-purewhite;
        }
    }

    .outline-cta--bright-square {
        @include cta-outlined-light-bg();
        border-radius: 0;
    }

    .cta-block--inline {
        .cta-block__cta {
            display: inline-block;

            &:not(:last-of-type) {
                margin-right: $measure--basic;
            }
        }
    }

    .outline-cta--bright-red-ol {
        @include cta-outlined-light-bg();
        border-color: $color-red50;

        &:active,
        &:hover {
            border-color: $color-red50;
            background-color: $color-red50;
        }
    }

    .outline-cta--mixed-red-gray {
        @include cta-outlined-light-bg($color-gray11, $color-red50);
    }

    .outline-cta-play--dark {
        @include cta-outlined-icon-dark-bg(
            $color-purewhite,
            $color-purewhite,
            $color-gray11,
            '\f144'
        );
    }

    .outline-cta-play--light {
        @include cta-outlined-icon-light-bg(
            $color-gray11,
            $color-gray11,
            '\f144'
        );
    }
}
